<template>
  <div class="index">
    <van-overlay :show="show" @click="isShowOverLay(false)">
      <div class="wrapper">
        <div class="block">敬请期待……</div>
      </div>
    </van-overlay>
    <transition name="fade">
      <router-view></router-view>
    </transition>
    <transition name="fade">
    <ul class="bottom-nav" v-if="showBottomBar">
      <li>
        <router-link to="/home">
          <div class="icon">
            <van-icon name="wap-home-o" />
          </div>
          <div class="text">首页</div>
        </router-link>
      </li>
      <li>
        <router-link to="/community">
          <div class="icon">
            <van-icon name="flower-o" />
          </div>
          <div class="text">社区</div>
        </router-link>
      </li>
      <li>
        <div class="icon" @click="isShowOverLay(true)">
          <!-- <van-icon name="add-o" /> -->
          <img src="http://img.wdjimg.com/image/video/418d281e65bf010c38c7b07bdd7b6a94_0_0.png" alt="">
        </div>
      </li>

      <li>
        <router-link to="/notice">
          <div class="icon">
            <van-icon name="circle" />
          </div>
          <div class="text">通知</div>
        </router-link>
      </li>
      <li>
        <router-link to="/mine">
          <div class="icon">
            <van-icon name="contact" />
          </div>
          <div class="text">我的</div>
        </router-link>
      </li>
    </ul>
    </transition>
  </div>
</template>

<script>
  export default {
    name: "index",
    data() {
      return {

      };
    },
    computed: {
      showBottomBar() {
        return this.$store.state.showBottomBar;
      },
      show() {
        return this.$store.state.showOverLay;
      }
    },
    methods: {
      isShowOverLay(n) {
        this.$store.commit("setShowOverLay", n);
      }
    },

    created() {
      this.$store.commit("setShowBottomBar", true);
      let opanEyeData = JSON.parse(localStorage.getItem("opanEyeData"));
      if (opanEyeData) {
        this.$store.commit("setClassify", opanEyeData.classify);
      } else {
        this.axios
          .get(
            `https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/categories/`
          )
          .then(res => {
            this.$store.commit("setClassify", res.data);
            let opanEyeData = {
              classify: res.data
            };
            localStorage.setItem("opanEyeData", JSON.stringify(opanEyeData));
          });
      }
    }
  };
</script>

<style lang="less" scoped>
  .fade-enter-active,
  .fade-leave-active {
    transition: all .5s;
    

  }

  .fade-enter,
  .fade-leave-to {
    opacity: 0;
    
  }

  .index {
    background-color: #fafafa;
    .wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
    }

    .block {
      width: 200px;
      height: 120px;
      background-color: #fff;
      text-align: center;
      line-height: 120px;
    }

    .bottom-nav {
      padding: 5px 0;
      background-color: #fafafa;
      display: flex;
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;

      li {
        flex: 1;

        .icon {
          width: 40px;
          margin: 0 auto;

          img {
            width: 100%;
            display: block;
          }
        }

        a {
          text-align: center;
          color: #6b6b6b;

          &.router-link-active {
            color: #000;
            font-weight: bold;
          }

          .icon {
            font-size: 24px;
            line-height: 23px;
          }

          .text {
            font-size: 12px;
            line-height: 12px;
          }
        }
      }
    }
  }
</style>