import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    PlayingPageDate: {},
    isInHome: true,
    Classify: [],
    showBottomBar: true,
    ClassifyDetailsId: 0,
    RecommendedToday: {},
    CommintyCardDetailData: {},
    DayRank: [],
    PlayingVideoIndex:-1,
    SearchPlaydata:null,
    showOverLay:false,
    Relevant:[]//相关推荐
  },
  mutations: { 
    setRelevant(state,value){
      state.Relevant=value
    },
    setShowOverLay(state,value){
    state.showOverLay=value
  },
    setSearchPlaydata(state,value){
      state.SearchPlaydata=value
    },
    setPlayingVideoIndex(state,value){
      state.PlayingVideoIndex=value
    },
    setDayRank(state, value) {
      state.DayRank = value
    },
    setCommintyCardDetailData(state, value) {
      state.CommintyCardDetailData = value
    },
    setPlayingPageDate(state, value) {
      state.PlayingPageDate = value
    },
    setRecommendedToday(state, value) {
      state.RecommendedToday = value
    },
    setIsInHome(state, value) {
      state.isInHome = value
    },
    setClassify(state, value) {
      state.Classify = value
    },
    setClassifyDetailId(state, value) {
      state.ClassifyDetailsId = value
    },

    setShowBottomBar(state, value) {
      state.showBottomBar = value

    }
  },
  actions: {},
  modules: {}
})